  height: 350px;
  object-fit: cover;
}

.main-footer__wrapper {
  padding-top: 39px;
}

.main-footer__social-tag {
  text-decoration: none;
  font-style: normal;
  font-weight: 700;
  font-size: 21px;
  line-height: 26px;
  color: #000000;
}

@media (min-width: 768px) {
  .main-footer__wrapper {
    display: grid;
    grid-template-columns: 1fr 0.5fr;
    padding: 39px 5% 36px;
  }

  .main-footer__social-tag {
    text-align: left;
  }
}
@media (min-width: 768px) {
  .main-footer__wrapper {
    padding-left: 60px;
    padding-right: 60px;
  }
}
@media (min-width: 1200px) {
  .main-footer__wrapper {
    padding-left: 120px;
    padding-right: 120px;
  }
}
.social__list {
  flex-wrap: wrap;
  justify-content: space-between;
  list-style: none;
  margin: 0;
  padding: 34px 0 0;
}

.social__item {
  display: inline;
  margin: 0;
}

.social__item-link {
  text-decoration: none;
}

@media (min-width: 768px) {
  .social {
    display: grid;
    width: 100%;
    grid-template-columns: 1fr 1fr;
    align-items: center;
  }

  .social__list {
    padding: 0;
  }
}
.copyright {
  padding-top: 40px;
  display: flex;
  justify-content: center;
  align-items: center;
  width: auto;
}

.copyright__text {
  font-style: normal;
  font-weight: 400;
  font-size: 14px;
  line-height: 21px;
  text-transform: uppercase;
}

@media (min-width: 768px) {
  .copyright {
    padding-top: 0;
    justify-content: right;
  }
}
.feedback-form__wrapper {
  padding-top: 28px;
  padding-left: 20px;
  padding-right: 20px;
}

.feedback-form__block {
  border: none;
  padding: 0 0 28px;
  margin: 0;
  display: block;
  width: 100%;
  height: auto;
}

.feedback-form__block-title {
  border-top: 1px solid #e5e5e5;
  display: block;
  width: 100%;
  height: auto;
  padding-top: 24px;
  padding-bottom: 20px;
  font-style: normal;
  font-weight: 700;
  font-size: 18px;
  line-height: 26px;
  color: #000000;
}

.feedback-form__block-controls {
  padding-left: 0;
  padding-right: 0;
}

.feedback-form__submit-area {
  padding-top: 30px;
  padding-bottom: 15px;
  display: flex;
  flex-flow: column;
  justify-content: center;
  align-items: center;
}

.feedback-form__submit-area-note {
  text-transform: uppercase;
  font-style: normal;
  font-weight: 400;
  font-size: 12px;
  line-height: 21px;
}

@media (min-width: 768px) {
  .feedback-form__wrapper {
    padding-left: 60px;
    padding-right: 60px;
  }

  .feedback-form__block-title {
    border-top: none;
    width: auto;
    font-style: normal;
    font-weight: 700;
    font-size: 21px;
    line-height: 26px;
    padding-right: 5%;
    padding-left: 5%;
  }

  .feedback-form__form {
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-template-areas: "name contacts" "impression impression" "attraction attraction" "emotions emotions" "submit submit";
    grid-column-gap: 0;
  }

  .feedback-form__block:nth-child(1) {
    grid-area: name;
    border-right: 1px solid #e5e5e5;
  }
  .feedback-form__block:nth-child(1) .feedback-form__block-controls {
    padding-right: 40px;
  }
  .feedback-form__block:nth-child(2) {
    grid-area: contacts;
  }
  .feedback-form__block:nth-child(2) .feedback-form__block-controls {
    padding-left: 40px;
  }
  .feedback-form__block:nth-child(3) {
    grid-area: impression;
    border-top: 1px solid #e5e5e5;
  }
  .feedback-form__block:nth-child(3) .feedback-form__block-controls {
    display: flex;
    flex-flow: row;
    justify-content: center;
    flex-wrap: wrap;
  }
  .feedback-form__block:nth-child(4) {
    border-top: 1px solid #e5e5e5;
    grid-area: attraction;
  }
  .feedback-form__block:nth-child(4) .feedback-form__block-controls {
    display: grid;
    grid-template-columns: 1fr 1fr;
    justify-items: left;
    align-items: baseline;
  }
  .feedback-form__block:nth-child(5) {
    grid-area: emotions;
  }

  .feedback-form__submit-area {
    grid-area: submit;
    display: flex;
    flex-flow: row;
    align-items: baseline;
    justify-content: space-between;
  }

  .feedback-form__submit-area-note {
    font-style: normal;
    font-weight: 400;
    font-size: 14px;
    line-height: 21px;
  }
}
@media (min-width: 1200px) {
  .feedback-form__wrapper {
    padding-left: 120px;
    padding-right: 120px;
  }

  .feedback-form__block:nth-child(1) .feedback-form__block-controls {
    padding-right: 60px;
  }
  .feedback-form__block:nth-child(2) .feedback-form__block-controls {
    padding-left: 60px;
  }
}
.form-control {
  display: flex;
  flex-direction: column;
}

.form-control__label {
  font-style: normal;
  font-weight: 400;
  font-size: 14px;
  line-height: 21px;
  color: #333333;
  display: block;
  text-align: left;
  padding-left: 16px;
}

.form-control__label--flat {
  padding-left: 0;
}

.form-control__input {
  height: 58px;
  width: auto;
  background-color: #f2f2f2;
  border: none;
  font-style: normal;
  font-weight: 700;
  font-size: 18px;
  line-height: 26px;
  padding: 0 0 0 16px;
}
.form-control__input::placeholder {
  color: rgba(0, 0, 0, 0.3);
}

.form-control__textarea {
  height: 150px;
  width: auto;
  background-color: #f2f2f2;
  font-style: normal;
  font-weight: 700;
  font-size: 18px;
  line-height: 26px;
  border: none;
  resize: none;
  padding: 16px;
}
.form-control__textarea::placeholder {
  color: rgba(0, 0, 0, 0.3);
}

.form-control__input-group {
  position: relative;
  display: flex;
  flex-direction: row;
  width: 100%;
  justify-content: center;
}

.form-control__input-group .form-control__input {
  width: 100%;
}

.form-control__input-group-icon {
  display: flex;
  text-align: center;
  justify-content: center;
  align-items: center;
  height: 58px;
  width: 58px;
  background-color: #e6e6e6;
}

.button {
  height: fit-content;
  background-color: #81b3d2;
  color: #ffffff;
  padding: 16px 32px;
  text-transform: uppercase;
  text-decoration: none;
  white-space: nowrap;
  font-style: normal;
  font-weight: 700;
  font-size: 21px;
  line-height: 26px;
  border: none;
  max-width: 458px;
  width: 100%;
}
.button:hover {
  background-color: #669ec0;
}
.button:active {
  background-color: #5496bd;
  color: rgba(255, 255, 255, 0.3);
}
.button:disabled {
  background-color: #e5e5e5;
}

